import React from 'react';
import { Link } from 'react-router-dom';
import './FirstPage.css';
import picture from './img.jpg'
import logo from './unnamed.png';

export default function FirtsPage() {
    return (
        <>
            <div className='headerFirstPage'>
                <p>SERVICE STATION MODULE</p>
            </div>
            <div className='logo'>
                <img src={logo}></img>
            </div>
            <div className='cardBlock'>
                <p><span><b>PLEASE SCAN</b><br /></span><span>YOUR STAFF CARD</span></p>
                <Link to='/secondPage'><img src={picture}></img></Link>
                
            </div>
            <div className='footer'>
                <p>Last cloud sunc: 2021-05-14 14:54</p>
                <p>Reader: <span>Connected</span></p>
                <p>3A V5.5.8 - Not for distribution to third party.</p>
            </div>
        </>
        
    )
}
